import axios from 'axios';
import { v4 as uuidv4 } from 'uuid';

import { DragLimits, ApiKey, CoverImage, ToolbarConfigParams } from 'types';

const url = 'https://api.discogs.com/database/search';

type BandSearchParams = Array<{
  artist: string;
  album: string;
}>;

export const getAlbums = async (
  bandArray: BandSearchParams,
  draglimits: DragLimits,
  configs: ToolbarConfigParams,
  apiKey: ApiKey,
): Promise<Array<CoverImage>> => {
  const albums = await Promise.all(
    bandArray.map((band) => {
      const searchString = `${band.artist} - ${band.album}`;
      const params = {
        q: searchString,
        type: 'release',
      };

      return axios.get(`${url}?${new URLSearchParams(params)}`, {
        headers: {
          Authorization: `Discogs key=${apiKey.DiscogsKey}, secret=${apiKey.DiscogsSecret}`,
          Accept: 'application/json',
          'Content-Type': 'application/json',
        },
      });
    }),
  );

  return albums.flatMap((album, index) => {
    const {
      data: { results },
    } = album;
    if (results?.length && results[0].master_id !== 0) {
      const [currentArtist, currentAlbum] = results[0].title
        .split('-')
        .map((str: string) => str.trim());

      return {
        id: uuidv4(),
        link: results[0].cover_image,
        x: draglimits.x + configs.size / 2,
        y: draglimits.y + configs.size / 2,
        artistLabel: {
          originalText: currentArtist,
          searchText: bandArray[index].artist,
          text: currentArtist,
          x: 0,
          y: 0,
        },
        albumLabel: {
          originalText: currentAlbum,
          searchText: bandArray[index].album,
          text: currentAlbum,
          x: 0,
          y: 0,
        },
      };
    }

    return [];
  });
};
