import React, { useState } from 'react';

import { TextLabel } from 'components';
import { useCoverContext, useSizesContext } from 'contexts';

export const TitleLabel: React.FC = () => {
  const { title, setTitle, configs } = useCoverContext();
  const { dragLimits, windowSize, initialY, fontSize } = useSizesContext();
  const [open, setOpen] = useState(false);

  const handleReset = () => {
    setTitle((currentTitle) => ({
      ...currentTitle,
      x: 0,
      y: 0,
    }));
  };

  if (!configs.showTitle || !title) return null;

  return (
    <TextLabel
      open={open}
      setOpen={setOpen}
      onReset={handleReset}
      label={title}
      setLabel={setTitle}
      pos={{
        x: windowSize.width / 2 - dragLimits.width / 4,
        y: initialY - fontSize,
        width: dragLimits.width / 2,
      }}
      min={{
        x: -dragLimits.width / 2 + dragLimits.x,
        y: initialY - 3 * fontSize,
      }}
      max={{
        x: dragLimits.width / 2 - dragLimits.x / 2,
        y: initialY - 3 * fontSize,
      }}
      labelSize={2}
    />
  );
};
