import React, { useState } from 'react';
import { Modal, TextField, Button } from '@mui/material';

import { testDiscogsAPICredentials } from 'api';
import { useApiContext, useToastContext } from 'contexts';

interface ApiInputProps {
  open: boolean;
}

export const ApiInputPopover: React.FC<ApiInputProps> = ({ open }) => {
  const { setApikeys } = useApiContext();
  const [apiKeyText, setApiKeyText] = useState('');
  const [apiSecretText, setApiSecretText] = useState('');
  const [loading, setLoading] = useState(false);
  const { showErrorMessage } = useToastContext();

  const handApiTextChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setApiKeyText(event.target.value);
  };

  const handSecretTextChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setApiSecretText(event.target.value);
  };

  const handleSubmit = async (evt: React.SyntheticEvent<HTMLFormElement>) => {
    evt.preventDefault();

    setLoading(true);
    const apiPayload = {
      DiscogsKey: apiKeyText,
      DiscogsSecret: apiSecretText,
    };

    try {
      await testDiscogsAPICredentials(apiPayload);

      setApikeys({
        DiscogsKey: apiKeyText,
        DiscogsSecret: apiSecretText,
      });
    } catch (err) {
      showErrorMessage('Discogs Api key or secret is invalid.');
    } finally {
      setLoading(false);
    }
  };

  const disabledInput = !apiKeyText || !apiSecretText;

  return (
    <Modal open={open}>
      <form
        onSubmit={handleSubmit}
        style={{
          position: 'absolute',
          top: '50%',
          left: '50%',
          transform: 'translate(-50%, -50%)',
          background: 'white',
          padding: '20px',
          borderRadius: '5px',
        }}>
        <h2> Enter Discogs API Key and API Secret </h2>
        <TextField
          label="Api Key"
          value={apiKeyText}
          onChange={handApiTextChange}
          fullWidth
          style={{ marginBottom: '20px' }}
        />
        <TextField
          label="Api Secret"
          value={apiSecretText}
          onChange={handSecretTextChange}
          fullWidth
          style={{ marginBottom: '20px' }}
        />
        <Button
          variant="contained"
          color="primary"
          type="submit"
          disabled={disabledInput || loading}>
          Submit
        </Button>
      </form>
    </Modal>
  );
};
