import React from 'react';

import { useCoverContext, useSizesContext } from 'contexts';
import { CoverImage, LabelType } from 'types';
import { AlbumCoverDrawLine, AlbumCoverImage, AlbumCoverLabel } from '.';
import { DraggableGroup } from 'components';

interface CoverImageProps {
  albumCover: CoverImage;
}

export const AlbumCover: React.FC<CoverImageProps> = ({ albumCover }) => {
  const { id } = albumCover;
  const { setCover, configs } = useCoverContext();
  const { fontSize, dragLimits, coverSize } = useSizesContext();

  const handleDragEnd = (update: CoverImage) => {
    setCover((currentCover) =>
      currentCover.map((star) => {
        return albumCover.id === star.id ? { ...update } : star;
      }),
    );
  };

  return (
    <DraggableGroup
      update={albumCover}
      setUpdate={handleDragEnd}
      min={{
        x: dragLimits.x,
        y: dragLimits.y,
      }}
      max={{
        x: dragLimits.width - coverSize / 2,
        y: dragLimits.height - coverSize / 2,
      }}>
      <AlbumCoverDrawLine id={id} />
      <AlbumCoverImage albumCover={albumCover} />
      {configs.showArtist && (
        <AlbumCoverLabel
          coverLabel={LabelType.ARTIST}
          albumCover={albumCover}
        />
      )}
      {configs.showAlbum && (
        <AlbumCoverLabel
          coverLabel={LabelType.ALBUM}
          albumCover={albumCover}
          offset={1.5 * fontSize}
        />
      )}
    </DraggableGroup>
  );
};
