import React, { useState } from 'react';

import { useCoverContext, useSizesContext } from 'contexts';
import { CoverImage, LabelType } from 'types';
import { TextLabel } from 'components';

interface AlbumCoverLabelProps {
  albumCover: CoverImage;
  coverLabel: LabelType;
  offset?: number;
}

export const AlbumCoverLabel: React.FC<AlbumCoverLabelProps> = ({
  albumCover,
  coverLabel,
  offset = 0,
}) => {
  const { setCover, erase, editLines } = useCoverContext();
  const { coverSize, fontSize } = useSizesContext();
  const [open, setOpen] = useState(false);

  const handleReset = () => {
    setCover((currentCover) =>
      currentCover.map((star) =>
        star.id === albumCover.id
          ? {
              ...star,
              [coverLabel]: {
                ...albumCover[coverLabel],
                x: 0,
                y: 0,
                text: albumCover[coverLabel].originalText,
              },
            }
          : star,
      ),
    );
  };

  if (erase || editLines) return null;

  return (
    <TextLabel
      open={open}
      setOpen={setOpen}
      label={albumCover[coverLabel]}
      onReset={handleReset}
      setLabel={(label) => {
        setCover((currentCover) =>
          currentCover.map((star) => {
            return albumCover.id === star.id
              ? {
                  ...star,
                  [coverLabel]: label,
                }
              : star;
          }),
        );
      }}
      pos={{
        x: -coverSize / 2,
        y: coverSize + offset + fontSize / 2,
        width: coverSize * 2,
      }}
      min={{
        x: albumCover.x - 2 * coverSize - 2 * fontSize,
        y: albumCover.y - 3 * coverSize,
      }}
      max={{
        x: albumCover.x + 2 * coverSize + 2 * fontSize,
        y: albumCover.y + coverSize,
      }}
    />
  );
};
