import React, { useState } from 'react';
import { Group, Rect } from 'react-konva';
import { v4 as uuidv4 } from 'uuid';

import { useCoverContext, useSizesContext } from 'contexts';
import { CoverImage, PosTypes } from 'types';

interface AlbumCoverDrawLineProps {
  id: CoverImage['id'];
}

export const AlbumCoverDrawLine: React.FC<AlbumCoverDrawLineProps> = ({
  id,
}) => {
  const { erase, points, setPoints, editLines, setLines, configs } =
    useCoverContext();
  const { coverSize } = useSizesContext();
  const [selection, setSelection] = useState<PosTypes | null>(null);

  React.useEffect(() => {
    !points && setSelection(null);
  }, [points]);

  const handleDrawLine = (id: string, pos: PosTypes) => {
    if (!points) {
      setPoints({ id, pos });
      setSelection(pos);
    } else if (points.id !== id) {
      setLines((currentLines) => {
        const lineCopy = [...currentLines];

        if (
          lineCopy.find(
            (currentLine) => currentLine.target.id === id && points.id === id,
          )
        ) {
          return lineCopy;
        }

        const foundLine = lineCopy.find(
          (currentLine) =>
            currentLine.target.id === id && points.id === currentLine.origin.id,
        );
        if (foundLine) {
          foundLine.origin = points;
          foundLine.target = { id, pos };
          return lineCopy;
        }

        const foundLineReverse = lineCopy.find(
          (currentLine) =>
            currentLine.origin.id === id && points.id === currentLine.target.id,
        );
        if (foundLineReverse) {
          foundLineReverse.origin = points;
          foundLineReverse.target = { id, pos };
          return lineCopy;
        }

        return [
          ...lineCopy,
          {
            label: {
              text: '',
              x: 0,
              y: 0,
            },
            origin: { ...points },
            target: { id, pos },
            id: uuidv4(),
          },
        ];
      });
      setPoints(null);
    }
  };

  if (!editLines) return null;

  return (
    <Group listening={!erase}>
      <Rect
        x={0 + coverSize / 2}
        y={0 - coverSize / 4 - coverSize / 8}
        width={coverSize / 2}
        height={coverSize / 2}
        fill={selection === PosTypes.TOP ? 'red' : 'white'}
        rotation={45}
        opacity={0.05}
        visible={!(!!selection && selection !== PosTypes.TOP)}
        onClick={() => handleDrawLine(id, PosTypes.TOP)}
      />
      <Rect
        x={0 + coverSize}
        y={0 + coverSize / 8}
        width={coverSize / 2}
        height={coverSize / 2}
        fill={selection && selection === PosTypes.RIGHT ? 'red' : 'white'}
        rotation={45}
        opacity={0.05}
        visible={!(!!selection && selection !== PosTypes.RIGHT)}
        onClick={() => handleDrawLine(id, PosTypes.RIGHT)}
      />
      <Rect
        x={0}
        y={0 + coverSize / 8}
        width={coverSize / 2}
        height={coverSize / 2}
        fill={selection === PosTypes.LEFT ? 'red' : 'white'}
        rotation={45}
        opacity={0.05}
        visible={!(!!selection && selection !== PosTypes.LEFT)}
        onClick={() => handleDrawLine(id, PosTypes.LEFT)}
      />
      <Rect
        x={0 + coverSize / 2}
        y={0 + coverSize - coverSize / 4 - coverSize / 8}
        width={coverSize / 2}
        height={coverSize / 2}
        fill={selection === PosTypes.BOTTOM ? 'red' : 'white'}
        rotation={45}
        opacity={0.05}
        visible={!(!!selection && selection !== PosTypes.BOTTOM)}
        onClick={() => handleDrawLine(id, PosTypes.BOTTOM)}
      />
    </Group>
  );
};
