import { Html } from 'react-konva-utils';

import { useCoverContext, useToolbarContext } from 'contexts';
import { ToolbarConfigPopover } from '.';
import { ToolbarConfigParams } from 'types';

export const ToolbarConfig: React.FC = () => {
  const { openResize, setOpenResize } = useToolbarContext();
  const {
    setConfigs,
    resetConfigs,
    configs,
    setCover,
    setLines,
    setTitle,
    resetTitle,
    title,
  } = useCoverContext();

  const handleCoverSize = (config: ToolbarConfigParams) => {
    if (config.showTitle === true && title.text === '') {
      resetTitle();
    }
    setConfigs({
      ...config,
    });
  };

  const handleResetElements = () => {
    setCover((currentCover) =>
      currentCover.map((star) => ({
        ...star,
        artistLabel: {
          ...star.artistLabel,
          x: 0,
          y: 0,
          text: star.artistLabel.originalText,
        },
        albumLabel: {
          ...star.albumLabel,
          x: 0,
          y: 0,
          text: star.albumLabel.originalText,
        },
      })),
    );

    setLines((currentLines) =>
      currentLines.map((currentLine) => ({
        ...currentLine,
        label: {
          ...currentLine.label,
          x: 0,
          y: 0,
        },
      })),
    );

    setTitle((currentTitle) => ({
      ...currentTitle,
      x: 0,
      y: 0,
    }));
  };

  if (!openResize) return null;

  return (
    <Html>
      <ToolbarConfigPopover
        open={openResize}
        onClose={() => setOpenResize(false)}
        onSubmit={handleCoverSize}
        config={configs}
        handleResetSettings={resetConfigs}
        handleResetElements={handleResetElements}
      />
    </Html>
  );
};
