export interface DragLimits {
  x: number;
  y: number;
  width: number;
  height: number;
}

export interface Point {
  id: string;
  pos: PosTypes;
}

export enum Colors {
  YELLOW = 'yellow',
  RED = '#DC143C',
  GREEN = '#32CD32',
  PINK = '#FF69B4',
  GOLD = '#FFD700',
  BLUE = '#1E90FF',
  PURPLE = '#9370DB',
  ORANGE = '#FF6347',
}

export enum BackColors {
  DARK = '#282c34',
}

export interface LabelParams {
  text: string;
  x: number;
  y: number;
}

export interface LabelParamsOriginal {
  originalText: string;
  searchText: string;
  text: string;
  x: number;
  y: number;
}

export interface LinePoint {
  id: string;
  origin: Point;
  target: Point;
  label: LabelParams;
}

export interface ApiKey {
  DiscogsKey: string;
  DiscogsSecret: string;
}

export enum LabelType {
  ARTIST = 'artistLabel',
  ALBUM = 'albumLabel',
}

export interface CoverImage {
  id: string;
  link: string;
  x: number;
  y: number;
  [LabelType.ARTIST]: LabelParamsOriginal;
  [LabelType.ALBUM]: LabelParamsOriginal;
}

export enum PosTypes {
  LEFT = 'left',
  RIGHT = 'right',
  TOP = 'top',
  BOTTOM = 'bottom',
}

export enum PopupState {
  ARTIST = 'artist',
  ALBUM = 'album',
}

export interface SearchParams {
  [PopupState.ARTIST]: string;
  [PopupState.ALBUM]: string;
}

export interface TooltipValues {
  text: string;
  x: number;
  y: number;
}

export enum ToolConfigIDs {
  SEARCH = 'search',
  RESIZE = 'resize',
  SHARE = 'share',
  ARROW = 'arrow',
  ERASE = 'erase',
}

export interface ToolConfig {
  id: ToolConfigIDs;
  tooltip: string;
  color: string;
  emoji: string;
  value: boolean;
  valueModifier: React.Dispatch<React.SetStateAction<boolean>>;
  reverse: boolean;
}

export interface LineParams {
  midX: number;
  midY: number;
  points: number[];
}

export enum LocalStorageKeys {
  TITLE = 'title',
  ALBUMS = 'albums',
  LINES = 'lines',
  CONFIG = 'config',
}

export enum ToolbarConfigValues {
  TITLE = 'title',
  SIZE = 'size',
  COLOR = 'color',
  BACK_COLOR = 'backColor',
  SHOW_TITLE = 'showTitle',
  SHOW_ARTIST = 'showArtist',
  SHOW_ALBUM = 'showAlbum',
}

export interface ToolbarConfigParams {
  [ToolbarConfigValues.TITLE]: string;
  [ToolbarConfigValues.SIZE]: number;
  [ToolbarConfigValues.COLOR]: Colors;
  [ToolbarConfigValues.BACK_COLOR]: BackColors;
  [ToolbarConfigValues.SHOW_TITLE]: boolean;
  [ToolbarConfigValues.SHOW_ARTIST]: boolean;
  [ToolbarConfigValues.SHOW_ALBUM]: boolean;
}
