import { useCoverContext, useSizesContext } from 'contexts';
import { Text } from 'react-konva';
import { Html } from 'react-konva-utils';
import { TextLabelPopover } from '.';
import { KonvaEventObject } from 'konva/lib/Node';
import { LabelParams } from 'types';
import { DraggableGroup } from '../DraggableGroup';

interface TitleTexProps {
  label: LabelParams;
  setLabel: (title: LabelParams) => void;
  open: boolean;
  setOpen: (open: boolean) => void;
  pos: {
    x: number;
    y: number;
    width: number;
  };
  min: {
    x: number;
    y: number;
  };
  max: {
    x: number;
    y: number;
  };
  labelSize?: number;
  onReset: () => void;
}

export const TextLabel: React.FC<TitleTexProps> = ({
  label,
  setLabel,
  pos,
  min,
  max,
  open,
  setOpen,
  labelSize = 1,
  onReset,
}) => {
  const { fontSize } = useSizesContext();
  const { configs } = useCoverContext();

  const handleSubmit = (text: string) => {
    setOpen(false);
    setLabel({
      ...label,
      text,
    });
  };

  return (
    <DraggableGroup update={label} setUpdate={setLabel} min={min} max={max}>
      <Text
        align="center"
        text={label.text}
        x={pos.x}
        y={pos.y}
        width={pos.width}
        fontSize={fontSize * labelSize}
        fill={configs.color}
        onClick={() => setOpen(true)}
        onMouseMove={(evt: KonvaEventObject<MouseEvent>) => {
          const container = evt.target.getStage()?.container();

          if (container) {
            container.style.cursor = 'pointer';
          }
        }}
        onMouseLeave={(evt: KonvaEventObject<MouseEvent>) => {
          const container = evt.target.getStage()?.container();

          if (container) {
            container.style.cursor = 'default';
          }
        }}
      />
      <Html>
        {open && (
          <TextLabelPopover
            open={open}
            onClose={() => setOpen(false)}
            onSubmit={handleSubmit}
            defaultText={label.text}
            onReset={() => {
              onReset();
              setOpen(false);
            }}
          />
        )}
      </Html>
    </DraggableGroup>
  );
};
