import { Html } from 'react-konva-utils';

import { useCoverContext, useToastContext, useToolbarContext } from 'contexts';
import { ToolbarSharePopover } from '.';
import { LocalStorageKeys } from 'types';

export const ToolbarShare: React.FC = () => {
  const {
    cover,
    setCover,
    lines,
    setLines,
    setTitle,
    title,
    setConfigs,
    configs,
  } = useCoverContext();
  const { showSuccessMessage, showErrorMessage } = useToastContext();
  const { openShare, setOpenShare } = useToolbarContext();

  const handleImport = (data: string) => {
    try {
      const parsedData = JSON.parse(data);

      if (
        parsedData[LocalStorageKeys.ALBUMS] &&
        parsedData[LocalStorageKeys.LINES] &&
        parsedData[LocalStorageKeys.CONFIG] &&
        parsedData[LocalStorageKeys.TITLE]
      ) {
        setCover(parsedData[LocalStorageKeys.ALBUMS]);
        setLines(parsedData[LocalStorageKeys.LINES]);
        setConfigs(parsedData[LocalStorageKeys.CONFIG]);
        setTitle(parsedData[LocalStorageKeys.TITLE]);
      }
    } catch (err) {
      showErrorMessage('invalid JSON Data');
    }
  };

  const handleCopy = (success: boolean) => {
    success
      ? showSuccessMessage('Text copied with success')
      : showErrorMessage('Error copying text');
  };

  if (!openShare) return null;

  return (
    <Html>
      <ToolbarSharePopover
        title={title}
        open={openShare}
        onClose={() => setOpenShare(false)}
        cover={cover}
        lines={lines}
        configs={configs}
        handleImport={handleImport}
        handleCopy={handleCopy}
      />
    </Html>
  );
};
