import React, {
  createContext,
  useState,
  useEffect,
  useContext,
  Dispatch,
  SetStateAction,
} from 'react';
import { useLocalStorage } from 'usehooks-ts';

import {
  CoverImage,
  Point,
  LinePoint,
  LocalStorageKeys,
  Colors,
  BackColors,
  LabelParams,
  ToolbarConfigParams,
} from 'types';

interface CoverContextData {
  cover: Array<CoverImage>;
  setCover: Dispatch<SetStateAction<Array<CoverImage>>>;
  lines: Array<LinePoint>;
  setLines: Dispatch<SetStateAction<Array<LinePoint>>>;
  erase: boolean;
  setErase: Dispatch<SetStateAction<boolean>>;
  points: Point | null;
  setPoints: Dispatch<SetStateAction<Point | null>>;
  editLines: boolean;
  setEditLines: Dispatch<SetStateAction<boolean>>;
  title: LabelParams;
  setTitle: Dispatch<SetStateAction<LabelParams>>;
  configs: ToolbarConfigParams;
  setConfigs: Dispatch<SetStateAction<ToolbarConfigParams>>;
  resetConfigs: () => void;
  resetTitle: () => void;
}

const CoverContext = createContext<CoverContextData>({} as CoverContextData);

const initialConfigValues = {
  size: 100,
  title: 'Album covers',
  color: Colors.YELLOW,
  backColor: BackColors.DARK,
  showArtist: true,
  showAlbum: true,
  showTitle: true,
};

const initialTitleValues = {
  text: 'Album covers',
  x: 0,
  y: 0,
};

export const useCoverContext = () => {
  const context = useContext(CoverContext);
  if (!context) {
    throw new Error('useCoverContext must be used within a CoverProvider');
  }
  return context;
};

export const CoverProvider: React.FC<{ children: React.ReactNode }> = ({
  children,
}) => {
  const [configs, setConfigs] = useLocalStorage<ToolbarConfigParams>(
    LocalStorageKeys.CONFIG,
    {
      ...initialConfigValues,
    },
  );
  const [cover, setCover] = useLocalStorage<Array<CoverImage>>(
    LocalStorageKeys.ALBUMS,
    [],
  );
  const [lines, setLines] = useLocalStorage<Array<LinePoint>>(
    LocalStorageKeys.LINES,
    [],
  );
  const [title, setTitle] = useLocalStorage<LabelParams>(
    LocalStorageKeys.TITLE,
    {
      ...initialTitleValues,
    },
  );
  const [erase, setErase] = useState(false);
  const [editLines, setEditLines] = useState(false);
  const [points, setPoints] = useState<Point | null>(null);

  useEffect(() => {
    setPoints(null);
  }, [cover]);

  useEffect(() => {
    if (erase) {
      setEditLines(false);
    }
  }, [erase]);

  useEffect(() => {
    if (editLines) {
      setErase(false);
    }
  }, [editLines]);

  useEffect(() => {
    !title.text &&
      setConfigs((currentConfig) => ({
        ...currentConfig,
        showTitle: false,
      }));
  }, [setConfigs, title]);

  return (
    <CoverContext.Provider
      value={{
        cover,
        setCover,
        erase,
        setErase,
        points,
        setPoints,
        editLines,
        setEditLines,
        lines,
        setLines,
        title,
        setTitle,
        resetTitle: () => setTitle({ ...initialTitleValues }),
        configs,
        setConfigs,
        resetConfigs: () => setConfigs({ ...initialConfigValues }),
      }}>
      {children}
    </CoverContext.Provider>
  );
};

