import React from 'react';

import { useCoverContext, useSizesContext } from 'contexts';
import { LineParams, LinePoint, LabelParams } from 'types';
import { TextLabel } from 'components';

interface LineProps {
  line: LinePoint;
  open: boolean;
  setOpen: (open: boolean) => void;
  lineParams: LineParams;
}

export const DrawLineLabel: React.FC<LineProps> = ({
  line,
  lineParams,
  open,
  setOpen,
}) => {
  const { coverSize, fontSize } = useSizesContext();
  const { setLines } = useCoverContext();

  const handleUpdateLabel = (label: LabelParams) => {
    setLines((currentLines) =>
      currentLines.map((currentLine) => {
        if (currentLine.id === line.id) {
          return {
            ...currentLine,
            label,
          };
        }
        return currentLine;
      }),
    );
  };

  const handleReset = () => {
    setLines((currentLines) =>
      currentLines.map((currentLine) => {
        if (currentLine.id === line.id) {
          return {
            ...currentLine,
            label: {
              ...currentLine.label,
              x: 0,
              y: 0,
            },
          };
        }
        return currentLine;
      }),
    );
  };

  return (
    <TextLabel
      open={open}
      setOpen={setOpen}
      onReset={handleReset}
      label={line.label}
      setLabel={handleUpdateLabel}
      pos={{
        x: -coverSize,
        y: fontSize * 1.5,
        width: coverSize * 2,
      }}
      min={{
        x: lineParams.midX - 1.5 * coverSize,
        y: lineParams.midY - 1.5 * coverSize,
      }}
      max={{
        x: lineParams.midX + 1.5 * coverSize,
        y: lineParams.midY + 1.5 * coverSize,
      }}
    />
  );
};
