import { useApiContext, useCoverContext } from 'contexts';
import { ApiInputPopover } from 'ApiInputPopover';
import { CoverBoard } from 'CoverBoard';

function App() {
  const { apiKey } = useApiContext();
  const { configs } = useCoverContext();

  return (
    <div className="App" style={{ backgroundColor: configs.backColor }}>
      {apiKey.DiscogsKey && apiKey.DiscogsSecret ? (
        <CoverBoard />
      ) : (
        <ApiInputPopover open />
      )}
    </div>
  );
}
export default App;
